<template lang="html">
  <head-nav></head-nav>
  <div class="container">
    <div class="error" v-if="!noError">
      <div class="jumbotron">
        <h1 v-if="error.noFind">
          <span class="glyphicon glyphicon-warning-sign" aria-hidden="true"></span>
          Sorry! 请检查你的地址是否输入有误？没找到该问卷。</h1>
        <h1 v-if="error.noData">
          <span class="glyphicon glyphicon-warning-sign" aria-hidden="true"></span>
          Sorry! 数据加载异常，请检查您的网络！或者刷新一下网页试试！</h1>
        <p class="back-button"><a class="btn btn-primary btn-lg" v-link="{name: 'index'}" role="button">返回首页</a></p>
      </div>
    </div>
    <div class="content" v-if="noError">
      <a class="btn btn-default" v-link="{name: 'index'}" role="button">返回</a>
      <div class="page-header">
        <h3> {{ title }} <small>此统计只包含完整回收的数据(注意：本统计不包含文本题)</small></h3>
      </div>
      <div v-for="issue of issueKey" track-by="$index">
        <div class="panel panel-default" v-if="issue.type !== 'text'">
          <div class="panel-heading">
            <h3 class="panel-title">问题{{ $index + 1 }}: {{ issue.title }}</h3>
          </div>
          <div class="panel-body">
            <div class="row">
              <div class="col-md-6">
                <bar :issue-answer="issue" :index="$index"></bar>
              </div>
              <div class="col-md-6">
                <pie :issue-answer="issue" :index="$index"></pie>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="submit">
      <a class="btn btn-info btn-lg active center-block" role="button" v-link="{name: 'index'}">返回</a>
    </div>
  </div>
</template>

<script>
import headNav from './headNav'
import bar from './chartType/bar'
import pie from './chartType/pie'
export default {
  data () {
    return {
      title: '标题',
      issueData: [],
      issueKey: [],
      error: {
        noId: false,
        noData: false,
        noFind: true
      }
    }
  },
  computed: {
    noError: function () {
      if (!this.error.noId && !this.error.noData && !this.error.noFind) {
        return true
      } else {
        return false
      }
    }
  },
  created () {
    let id = this.$route.query.id
    let data = JSON.parse(global.localStorage.getItem('questionnaireData'))
    if (id && data) {
      for (var i = 0; i < data.length; i++) {
        if (data[i].questionnaireId === id) {
          this.error.noFind = false
          this.title = data[i].questionnaireTitle
          this.issueData = data[i].data
        }
      }
    } else {
      if (id) {
        this.error.noData = true
        this.error.noFind = false
      }
    }
    // 初始化数据结构
    for (let i = 0; i < this.issueData[0].length; i++) {
      this.issueKey[i] = {}
      this.issueKey[i].title = this.issueData[0][i].date.itemVal
      this.issueKey[i].type = this.issueData[0][i].type
      this.issueKey[i].option = this.issueData[0][i].date.radioValArry
      this.issueKey[i].pickedVal = []
    }
    // 把答案 推入 issueKey里面
    for (let i = 0; i < this.issueData.length; i++) {
      for (let j = 0; j < this.issueKey.length; j++) {
        this.issueKey[j].pickedVal.push(this.issueData[i][j].date.pickedVal)
      }
    }
  },
  components: {
    headNav,
    bar,
    pie
  }
}
</script>

<style lang="css" scoped>
.container{
  box-shadow: 0 0  6px 2px #999;
  padding: 10px;
  margin: 20px auto;
}
.page-header{
  text-align: center;
}
.page-header small{
  display: block;
  margin-top: 10px;
}
.error .glyphicon-warning-sign{
  color: #f00;
}
.error .back-button{
  margin-top: 10%;
  margin-left: 45%;
}
.panel-body{
  overflow: hidden;
}
.submit{
  width: 80%;
  margin: 50px 10% 20px 10%;
}
</style>



