<template lang="html">
  <div class="pie">
    <canvas :id="domId" width="400" height="400"></canvas>
  </div>
</template>

<script>
var Chart = require('chart.js')
export default {
  data () {
    return {
      domId: 'myChart-bar' + this.index
    }
  },
  // 父组件的数据
  props: ['issueAnswer', 'index'],
  computed: {
    num: function () {
      let val = []
      if (this.issueAnswer.type === 'radio') {
        for (let i = 0; i < this.issueAnswer.option.length; i++) {
          val[i] = 0
        }
        for (let i = 0; i < this.issueAnswer.option.length; i++) {
          for (let j = 0; j < this.issueAnswer.pickedVal.length; j++) {
            if (this.issueAnswer.pickedVal[j] === this.issueAnswer.option[i]) {
              val[i]++
            }
          }
        }
      }
      if (this.issueAnswer.type === 'checkbox') {
        for (let i = 0; i < this.issueAnswer.option.length; i++) {
          val[i] = 0
        }
        for (let i = 0; i < this.issueAnswer.option.length; i++) {
          for (let j = 0; j < this.issueAnswer.pickedVal.length; j++) {
            for (let k = 0; k < this.issueAnswer.pickedVal[j].length; k++) {
              if (this.issueAnswer.pickedVal[j][k] === this.issueAnswer.option[i]) {
                val[i]++
              }
            }
          }
        }
      }
      return val
    }
  },
  ready () {
    var ctx = document.getElementById(this.domId)
    var myDoughnutChart = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: this.issueAnswer.option,
        datasets: [
          {
            label: '选择的人数',
            data: this.num,
            backgroundColor: [
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)'
            ],
            borderColor: [
              'rgba(255,99,132,1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)'
            ],
            borderWidth: 1
          }]
      },
      options: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            }
          }]
        },
        responsive: true,
        maintainAspectRatio: false
      }
    })
    myDoughnutChart
  }
}
</script>

<style lang="css" scoped>
</style>



